.gallery {
    display: flex;
    justify-content: center;
    padding: 30px 0;
    max-width: 768px;
    min-height: 196px;
    background-image: linear-gradient(
        to bottom,
        #FFF5F6 30%,
        #ffffff 30%
        );

    @include device(768px){
        max-width: 768px;
        min-height: 387px;
            background-image: linear-gradient(to bottom,
                    #FFF5F6 55%,
                    #ffffff 55%);
    }

    @include device(1200px) {
        min-width: 100%;
        min-height: 550px;
        background-image: linear-gradient(to bottom,
                #FFF5F6 24%,
                #ffffff 24%);
    }
}

.grid {
    display: flex;
    flex-direction: column;
    margin: auto 0;
}

.grid__item {
    margin-right: 3px;
    @include device(768px) {
        margin-right: 6px;
    }
}

.grid__item-right {
    margin-right: 0;
}

.grid__item-top {
    margin-bottom: 3px;
    @include device(768px) {
            margin-bottom: 6px;
        }
}

